.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    column-gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    height: 100%;
    width: 100%;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    cursor: pointer;
}

.about__me-image:hover{
    transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.about__card{
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    background: var(--color-bg-variant);
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about__icon{
    font-size: 1.4rem;
    color: var(--color-primary);
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
}

.about__contents p{
    font-size: 0.8rem;
    margin-bottom: 2rem;
}

/*==========================MEDIA QUERY (MEDIUM DEVICES)==============================*/
@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        row-gap: 15%;
    }

    .about__me{
        width: 50%;
        margin: 0 auto -1.5rem;
    }

}

/*==========================MEDIA QUERY (SMALL DEVICES)===============================*/
@media screen and (max-width: 600px) {
    .about__me{
        width: 70%;
        margin-bottom: -2.5rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
    }

    .about__contents{
        text-align: center;
    }
    
    .about__contents p{
        margin: 1rem 0 1.5rem;
    }
    
}