header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*============================== CTA BUTTONS =================================*/
.buttons{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*============================== SOCIALS =================================*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

/*============================== MY PIC =================================*/
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 60rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
   /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
}

/*============================== SCROLL DOWN BUTTON =================================*/
.scroll__down{
    position: absolute;
    right: 0;
    bottom: 4rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/*==============================MEDIA QUERIES (MEDIUM DEVICES)=====================================*/
@media screen and (max-width: 1366px){
    header{
        padding-top: 5rem;
    }
    .buttons{
        margin-top: 1.5rem;
    }
    .me{
        margin-top: 2rem;
    }
}
/*==============================MEDIA QUERIES (MEDIUM DEVICES)=====================================*/
@media screen and (max-width: 1024px) {
    header{
        height: 90vh;
    }
}


/*==============================MEDIA QUERIES (SMALL DEVICES)=====================================*/
@media screen and (max-width: 600px) {
    header{
        height: 90vh;
    }

    .me{
        width: 18rem;
        left: calc(50% - 10rem);
        margin-left: 1rem;
        margin-right: 1rem;
    }

    .header__socials, .scroll__down{
        display: none;
    }
}